import React from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import { useStaticQuery, graphql } from 'gatsby';
import PostLink from '../components/PostLink';
import Title from '../components/Title';

const Projects = ({ isDesktop, isMobile }) => {
  const data = useStaticQuery(
    graphql`
      query {
        posts: allContentfulPost(sort: { fields: date, order: DESC }) {
          nodes {
            id
            images {
              id
              fluid(maxWidth: 1000) {
                src
                ...GatsbyContentfulFluid
              }
            }
            videos {
              file {
                url
              }
            }
            title
            date(formatString: "yyyy / M")
            description {
              description
            }
            url
            thumb {
              id
              fluid(maxWidth: 1000) {
                src
                ...GatsbyContentfulFluid
              }
            }
          }
        }
      }
    `
  );

  return (
    <section id="projects">
      <Container>
        <Title title={'All Posts'}></Title>
        {isDesktop && (
          <Row md={3}>
            <Fade right duration={500} delay={250} distance="30px">
              {data.posts.nodes.map((node) => {
                return (
                  <Col>
                    <PostLink node={node} isDesktop={isDesktop} isMobile={isMobile} />
                  </Col>
                );
              })}
            </Fade>
          </Row>
        )}
        {isMobile && (
          <>
            {data.posts.nodes.map((node) => {
              return <PostLink node={node} isDesktop={isDesktop} isMobile={isMobile} />;
            })}
          </>
        )}
      </Container>
    </section>
  );
};

export default Projects;
